<template>
  <div class="login">
    <h2 style="margin-top:-10vh;">Hello Again ....</h2>
    <form name="login" style="margin-top:10vh;">
      <div  v-if="otpSent == false" class="form-group">
        <div class="input-group">
          <input type="number" id="ph-number" v-model="phNum" required="required"/>
          <label class="control-label" for="ph-number">Your Phone Number</label><i class="bar"></i>
        </div>
      </div>
      <div v-if="otpSent == false" class="d-flex flex-column flex-lg-row align-items-center justify-content-between down-container" style="margin-bottom:30px;">
        <button class="btn btn-primary" id="sign-in-button" @click.prevent="signin" >
          Send Otp
        </button>
        <router-link class='link' :to="{name: 'signup'}">Create Account</router-link>
      </div>
      <div v-if="otpSent">
        <div class="form-group" >
          <div class="input-group">
            <input type="password" id="otp" required="required"/>
            <label class="control-label" for="otp">Otp Sent To Your Mobile</label><i class="bar"></i>
          </div>
        </div>
        <div class="d-flex flex-column flex-lg-row align-items-center justify-content-between down-container">
          <button class="btn btn-primary" type="submit">
            Login
          </button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
  export default {
    name: 'login',

    data () {
      return {
        otpSent: false,
        phNum: '',
      }
    },

    methods: {
      signin () {
      }
    }

  }
</script>

<style lang="scss">
  .login {
    @include media-breakpoint-down(md) {
      width: 100%;
      padding-right: 2rem;
      padding-left: 2rem;
      .down-container {
        .link {
          margin-top: 2rem;
        }
      }
    }

    h2 {
      text-align: center;
    }
    width: 21.375rem;

    .down-container {
      margin-top: 3.125rem;
    }
  }
</style>



